import React from 'react';
import DropDown from '../../common/DropDown';
import FILTERING_OPTIONS from '../../common/FilteringOptions';
import axios from 'axios';
import { SumbitModalProps } from '../../../types/TeamPageType';
import alertModal from '../../common/alertModal';
import {
  Modal,
  ModalPage,
  DetailPage,
  StyledHeader,
  StyledOption,
  StyledSubTitle,
  StyledDescription,
  StyledBlock,
  StyledInput,
  StyledSpan,
  StyledButtonContainer,
  StyledButton,
  StyledExitButton,
} from '../Styles/ComponentStyle';

function RegisterModal(props: SumbitModalProps) {
  const { groupId, setShowModal } = props;
  const [position, setPosition] = React.useState('');
  const [skill, setSkill] = React.useState('');
  const [memo, setMemo] = React.useState('');

  const config = {
    withCredentials: true,
  };

  function SubmitButton() {
    const validator = [];
    if (position === '' || '') {
      validator.push('');
    }
    if (skill === '' || '') {
      validator.push('');
    }
    if (memo === '') {
      validator.push('');
    }
    if (validator.length > 0) {
      alertModal(
        `[${validator.join(', ')}]    .`,
        'text'
      );
    } else {
      const data = {
        position: position,
        level: skill,
        contents: memo,
      };
      axios
        .post(
          `${process.env.REACT_APP_API_URL}/groups/${groupId}`,
          data,
          config
        )
        .then(async (res) => {
          const confirm = await alertModal(
            '  .',
            'text'
          );
          if (confirm) {
            setShowModal(false);
            window.location.reload();
          }
        })
        .catch((e) => {
          console.error('  : ', e);
          alertModal(
            `  . ${e.response.data.message}.`,
            'error'
          );
        });
    }
  }

  return (
    <>
      <Modal>
        <ModalPage>
          <StyledHeader>
            <h2> </h2>
            <StyledExitButton
              onClick={() => {
                setShowModal(false);
              }}
            >
              
            </StyledExitButton>
          </StyledHeader>
          <DetailPage>
            <StyledOption>
              <DropDown
                list={FILTERING_OPTIONS.findingTeam.position}
                selected={position}
                setSelected={setPosition}
              />

              <DropDown
                list={FILTERING_OPTIONS.findingTeam.skill}
                selected={skill}
                setSelected={setSkill}
              />
            </StyledOption>
            <StyledSubTitle>
              <StyledSpan></StyledSpan>
              <StyledInput
                onChange={(e) => {
                  setMemo(e.target.value);
                }}
              />
            </StyledSubTitle>
            <StyledDescription>
                    .
            </StyledDescription>
          </DetailPage>
          <StyledButtonContainer>
            <StyledButton
              onClick={() => {
                setShowModal(false);
              }}
            >
              
            </StyledButton>
            <StyledButton onClick={SubmitButton}></StyledButton>
          </StyledButtonContainer>
        </ModalPage>
      </Modal>
    </>
  );
}

export default RegisterModal;
