let timer: NodeJS.Timeout | null = null; //    

const debounce = (fn: Function, delay: number) => {
  return (...args: any[]) => {
    //(debounce ) 
    if (timer) {
      clearTimeout(timer); //      
      timer = null;
    }
    timer = setTimeout(() => {
      fn(...args); //   delay  func 
    }, delay);
  };
};

//  delay   ,      ,     .
// delay     ,     .

export default debounce;
