import { useCallback, useEffect, useRef } from 'react';
import debounce from './debounce';

type IntersectHandler = () => void;

const useIntersect = (
  onIntersect: IntersectHandler, //       
  options?: IntersectionObserverInit //Intersection Observer  
) => {
  let observerRef = useRef<IntersectionObserver | null>(null);
  const ref = useRef<HTMLDivElement>(null); // Intersection Observer  DOM 

  const callback = useCallback(
    (entries: IntersectionObserverEntry[]) => {
      const entry = entries[0];
      if (entry.isIntersecting) {
        onIntersect(); //   
      }
    },
    [onIntersect]
  );

  const debouncedCallback = debounce(callback, 300);

  // Intersection Observer 
  useEffect(() => {
    if (!ref.current) return; //  
    // eslint-disable-next-line
    observerRef.current = new IntersectionObserver(debouncedCallback, options);
    observerRef.current.observe(ref.current); // 
    return () => {
      //   
      observerRef.current && observerRef.current.disconnect();
    };
  }, [ref, options, callback]);

  return ref;
};

export default useIntersect;
